html, body {
  font-size: 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: "Open Sans", Arial, sans-serif;
}

button {
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
}

.header {
  margin-bottom: 32px;
}

.header.show-nav .sidebar {
  display: block;
}

.header.show-nav + section {
  left: 200px;
}

.header__title {
  font-family: "Quicksand", Helvetica, Arial, sans-serif;
  text-align: center;
  font-weight: 500;
  vertical-align: middle;
  margin: 0;
  padding: 24px 0;
}

.app-logo {
  vertical-align: middle;
  display: inline-block;
}

.app-logo .feather {
  top: -5px;
  position: relative;
  vertical-align: middle;
  fill: #e74c3c;
  stroke: #e74c3c;
}

.app-logo--lg .app-name {
  font-size: 3rem;
}

.app-logo--lg .feather {
  width: 45px;
  height: 45px;
}

.app-logo--lg .app-logo__link {
  font-size: 3rem;
  line-height: 1;
}

.app-logo--sm .app-name {
  font-size: 1.2rem;
}

.app-name {
  color: #2c2828;
}

.app-logo__link {
  text-decoration: none;
}

.tile {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 200px;
  position: relative;
  padding: 16px 0;
  width: 28%;
  box-sizing: border-box;
  color: #fff;
}

.tile:hover {
  -webkit-box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background: #fff;
}

.tile__img {
  display: block;
  max-width: 100%;
  width: 100px;
  margin: 0 auto;
  position: relative;
}

.tile__icon {
  display: block;
  width: 32px;
  height: 32px;
  margin: 0 auto 24px;
  stroke: #fff;
}

.tile__name {
  display: block;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: .01em;
  text-align: center;
  position: relative;
  margin: 0 auto;
  color: #6d6d6d;
  font-family: "Quicksand", Helvetica, Arial, sans-serif;
  font-weight: 500;
}

.tile__link {
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  width: 100%;
  height: 100%;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: #fff;
  position: relative;
}

.nav__tile {
  background: #e74c3c;
  /* fallback for old browsers */
  background-image: -webkit-gradient(linear, left bottom, left top, from(#e74c3c), to(#ff7f67));
  background-image: -webkit-linear-gradient(bottom, #e74c3c 0%, #ff7f67 100%);
  background-image: -o-linear-gradient(bottom, #e74c3c 0%, #ff7f67 100%);
  background-image: linear-gradient(to top, #e74c3c 0%, #ff7f67 100%);
  margin: 16px;
  border-radius: 8px;
}

.nav__tile:hover {
  -webkit-box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background: #e74b3b;
}

.nav__tile .tile__name {
  font-size: 1.5rem;
  color: #fff;
  font-weight: 400;
  letter-spacing: .03em;
}

.sidebar {
  position: fixed;
  height: 100%;
  width: 200px;
  display: none;
}

.sidebar__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.sidebar__item {
  padding: 16px 0 16px 16px;
  font-size: 1.3rem;
  font-family: "Quicksand", Helvetica, Arial, sans-serif;
  font-weight: 500;
}

.sidebar__link {
  text-decoration: none;
  color: #415058;
}

.sidebar__link.active {
  color: #e74c3c;
}

.sidebar__btn--toggle {
  padding: 8px;
}

.section {
  position: relative;
  padding: 16px;
}

@media (min-width: 768px) {
  .section {
    padding: 24px 16px;
  }
}

@media (min-width: 992px) {
  .section {
    padding: 32px 16px;
  }
}

.grid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}


